<template>
	<div id="user">
		<div class="top">
			<header>
				我的
			</header>
			<div class="userInfo">
				<img class='avator' src="http://gw.alicdn.com/sns_logo/i4/O1CN01LVo9Oy1oLJEQUq2Pe_!!0-mytaobao.jpg_100x100q90_.webp" alt="">
				<span class="username">咪斯特王</span>
				<div class="icon">
					<img src="https://gw.alicdn.com/tfs/TB1tcovekL0gK0jSZFxXXXWHVXa-54-54.png_48x48q90_.webp" alt="">
					<img src="https://gw.alicdn.com/tfs/TB1dbweXwHqK1RjSZFgXXa7JXXa-57-54.png_48x48q90_.webp" alt="">
				</div>
			</div>
			<div class="headerTools">
				<div>
					<span>{{collectionCount}}</span>
					收藏夹
				</div>
				<div>
					<span>1</span>
					关注店铺
				</div>
				<div>
					<span>59</span>
					足迹
				</div>
				<div>
					<span>2</span>
					红包卡券
				</div>
			</div>
		</div>
		<div class="main">
			<div class="myOrder">
				<div class="title">
					<span>我的订单</span>
					<span>查看全部订单<i class='iconfont icon-right'></i></span>
				</div>
				<div class="icons">
					<div>
						<img src="https://gw.alicdn.com/tfs/TB16GolmKuSBuNjy1XcXXcYjFXa-87-87.png_60x60q90_.webp" alt="">
						待付款
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1cwzgmMmTBuNjy1XbXXaMrVXa-87-87.png_60x60q90_.webp" alt="">
						待发货
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1b3zgmMmTBuNjy1XbXXaMrVXa-87-87.png_60x60q90_.webp" alt="">
						待收货
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1fOKqm_tYBeNjy1XdXXXXyVXa-87-87.png_60x60q90_.webp" alt="">
						评价
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1fMzgmMmTBuNjy1XbXXaMrVXa-87-87.png_60x60q90_.webp" alt="">
						退款/售后
					</div>
				</div>
			</div>
			<div class="myOrder tools">
				<div class="title">
					<span>必备工具</span>
					<span>查看全部工具<i class='iconfont icon-right'></i></span>
				</div>
				<div class="icons">
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1Jcbkj4D1gK0jSZFsXXbldVXa-96-96.png_70x70q90_.webp" alt="">
						每日返现
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1zT0hkkT2gK0jSZPcXXcKkpXa-96-96.png_70x70q90_.webp" alt="">
						领券中心
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1dcYebRSD3KVjSZFqXXc4bpXa-96-96.png_70x70q90_.webp" alt="">
						闲置换钱
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1csUri13tHKVjSZSgXXX4QFXa-96-96.png_70x70q90_.webp" alt="">
						客服小蜜
					</div>
					<div>
						<img src="https://gw.alicdn.com/tps/TB1rhCFPXXXXXa1XVXXXXXXXXXX-96-96.png_70x70q90_.webp" alt="">
						花呗
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1S6oYmQL0gK0jSZFxXXXWHVXa-96-96.png_70x70q90_.webp" alt="">
						我的快递
					</div>
					<div>
						<img src="https://img.alicdn.com/tfs/TB1zVfpj2b2gK0jSZK9XXaEgFXa-96-96.png_70x70q90_.webp" alt="">
						天猫农场
					</div>
					<div>
						<img src="https://gw.alicdn.com/tfs/TB1q0T8mpOWBuNjy0FiXXXFxVXa-96-96.png_70x70q90_.webp" alt="">
						主题换肤
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:'user',
		data(){
			return{
				collectionCount:0
			}
		},
		activated(){
			let count=localStorage.getItem('collectionList');
			if(count){
				this.collectionCount=JSON.parse(count).length;
			}
			document.documentElement.scrollTop=document.body.scrollTop=0;
		}
	}
</script>
<style lang='stylus' scoped>
	#user
		height:100vh;
		background:#f2f2f2;
	.top
		width: 100%;
		overflow:hidden;
		background: linear-gradient(to right, #FD9126, #FF5000);
		header
			font-size:.4rem;
			color:#fff;
			text-align:center;
			font-weight:700;
			padding-top:.2rem;
		.userInfo
			display:flex;
			padding:.2rem;
			margin-top:.2rem;
			position:relative;
			.avator
				width:1.3rem;
				height:1.3rem;
				border-radius:50%;
			.username
				font-size:.38rem;
				color:#fff;
				font-weight:700;
				margin-left:.2rem;
			.icon
				position: absolute;
				right:.2rem;
				top:.22rem;
				display:inherit;
				img
					width: .48rem;
					height:.48rem;
					margin-right:.3rem;
				img:first-child
					margin-right:.4rem;
		.headerTools
			font-size:.26rem;
			color:#fff;
			display:flex;
			margin:.2rem 0;
			margin-bottom:.3rem;
			div
				display:flex;
				flex-direction:column;
				width: 25%;
				align-items:center;
				span
					padding-bottom:.14rem;
			
	.main
		background:#f2f2f2;
		overflow:auto;
		padding-bottom:1.6rem;
		.myOrder
			width:92%;
			border-radius:.12rem;
			background:#fff;
			margin:0 auto;
			padding-bottom:.26rem;
			margin-top:.3rem;
			.title
				font-size:.28rem;
				display:flex;
				justify-content:space-between;
				border-bottom:1px solid #ddd;
				padding:.2rem;
				span:first-child
					color:rgb(51,51,51);
					font-weight:bold;
				span:last-child
					color:rgb(153,153,153);
					.iconfont
						font-size:inherit;
			.icons
				display:flex;
				align-items:center;
				margin:.2rem;
				div
					width: 20%;
					font-size:.26rem;
					display:flex;
					flex-direction:column;
					align-items:center;
					justify-content:center;
					margin:.2rem 0;
					img
						width:.62rem;
						height:.62rem;
						margin-bottom:.1rem;
		.tools
			height:auto;
			padding-bottom:.2rem;
			.icons
				flex-wrap:wrap;
				div
					width:25%;		
			
</style>


